import React from 'react'
import { Route, Redirect } from 'react-router-dom'

const ProtectedLogin = ({ component: Component, ...rest }) => {
    return (
        <Route {...rest}
            render={props =>
                !localStorage.getItem('isAuth') ? (
                    <Component {...props} />
                ) : (
                        <Redirect to="/" />
                    )

            }

        />
    )
}

export default ProtectedLogin 
