import { useState } from 'react'
import moment from 'moment'

const isEmptyValue = (value) => {
    return (
        value === undefined ||
        value === null ||
        (Array.isArray(value) && value.length === 0) ||
        (typeof value === 'object' && Object.keys(value).length === 0) ||
        (typeof value === 'string' && value.trim().length === 0) ||
        (typeof value === 'number' && value < 1)
    )
}

const formatDate = (value) => {
    const date = moment(value)

    if (date.isValid()) {
        return date.format('YYYY-MM-DD')
    }

    return false
}

const formatMonth = (value) => {
    const date = moment(value)

    if (date.isValid()) {
        return date.format('YYYY-MM')
    }

    return false
}

const formatDateTime = (value) => {
    const date = moment(value)

    if (date.isValid()) {
        return date.format('YYYY-MM-DD HH:mm:ss')
    }

    return false
}

const sleep = (milliseconds) => {
    return new Promise(resolve => setTimeout(resolve, milliseconds))
}

const diffDate = (new_value, old_value) => {
    const new_date = moment(new_value)
    const old_date = moment(old_value)

    if (new_date.isValid() && old_date.isValid()) {
        return new_date.diff(old_date)
    }

    return false
}

const secondToTime = (value) => {
    const second = moment.utc(value)

    if (second.isValid()) {
        return second.format('HH:mm:ss')
    }

    return false
}

const useLocalStorage = (key, initialValue) => {
    const [storedValue, setStoredValue] = useState(() => {
        try {
            const item = window.localStorage.getItem(key)
            return item ? JSON.parse(item) : initialValue
        } catch (err) {
            console.log(err)
            return initialValue
        }
    })

    const setValue = (value) => {
        try {
            const valueToStore = value instanceof Function ? value(storedValue) : value
            setStoredValue(valueToStore)
            window.localStorage.setItem(key, JSON.stringify(valueToStore))
        } catch (err) {
            console.log(err)
        }
    }

    return [storedValue, setValue]
}

export const generalService = {
    isEmptyValue,
    formatDate,
    formatDateTime,
    formatMonth,
    sleep,
    diffDate,
    secondToTime,
    useLocalStorage
}