import { useState, useEffect } from 'react'
import { Card, Button, Form, Table, Spinner } from 'react-bootstrap'
import _ from 'lodash'
import DatePicker from 'react-datepicker'
import { saveAs } from 'file-saver'
import { generalService } from './../../utils/services'
import { useCalls, useCampaigns, useExports, useUsers } from './../../utils/functions'
import { Pagination, Notification, CustomSelect } from './../../components'

const { isEmptyValue, formatDate, sleep } = generalService

export const ReportPerformanceAgent = (props) => {
    const fnCalls = useCalls()
    const fnCampaigns = useCampaigns()
    const fnExports = useExports()
    const fnUsers = useUsers()
    const { Error, Success, Warning } = Notification

    const [loading, setLoading] = useState(true)
    const [exporting, setExporting] = useState(false)
    const [notif, setNotif] = useState(initialNotif)
    const [tableData, setTableData] = useState({})
    const [formFilter, setFormFilter] = useState({
        user_id: "",
        campaign_id: "",
        start: formatDate(),
        end: formatDate(),
        disable_date: false
    })
    const [currentFilter, setCurrentFilter] = useState({
        page: 1,
        ...formFilter
    })
    const [optionAgent, setOptionAgent] = useState([{
        value: "",
        label: "Choose..."
    }])
    const [optionCampaign, setOptionCampaign] = useState([{
        value: "",
        label: "Choose..."
    }])

    useEffect(() => {
        const fetchData = async () => {
            await fnCalls.GetReportPerformanceAgent(currentFilter).then((res) => {
                setTableData(res)
                setLoading(false)
            }).catch((err) => { return }) //do nothing, let it go back to login
        }

        if (loading) fetchData()
        return () => setLoading(false)
    }, [loading])

    useEffect(() => {
        const fetchData = async () => {
            await fnUsers.Get({
                limit: 100,
                order: "username",
                is_active: 1
            }).then((res) => {
                if (!isEmptyValue(res.data)) {
                    let mapOptionUsers = res.data.map((row) => {
                        return { value: row.id, label: row.username }
                    })

                    setOptionAgent([
                        ...optionAgent,
                        ...mapOptionUsers
                    ])
                }
            }).catch((err) => { return }) //do nothing, let it go back to login

            await fnCampaigns.Get({
                limit: 100,
                order: "name"
            }).then((res) => {
                if (!isEmptyValue(res.data)) {
                    let mapOptionCampaigns = res.data.map((row) => {
                        return { value: row.id, label: row.name }
                    })

                    setOptionCampaign([
                        ...optionCampaign,
                        ...mapOptionCampaigns
                    ])
                }
            }).catch((err) => { return }) //do nothing, let it go back to login
        }

        fetchData()
        return () => {
            setOptionAgent([{
                value: "",
                label: "Choose..."
            }])
            setOptionCampaign([{
                value: "",
                label: "Choose..."
            }])
        }
    }, [])

    const handleChangeFormFilter = (key, val) => {
        if (key === 'start' && new Date(val) > new Date(formFilter.end)) {
            setFormFilter({
                ...formFilter,
                start: val,
                end: val
            })
        } else {
            setFormFilter({ ...formFilter, [key]: val })
        }
    }

    const handleSubmitFormFilter = (e) => {
        e.preventDefault()

        setCurrentFilter({
            ...currentFilter,
            ...formFilter,
            page: 1
        })
        setLoading(true)
    }

    const handleExport = async (limitData = false) => {
        await fnExports.ReportPerformanceAgent({
            ...currentFilter,
            limit: limitData || 20
        }).then(async (res) => {
            await sleep(5000)
            setExporting(false)

            if (res.success) {
                setNotif({
                    ...notif,
                    title: "Success",
                    message: "Export data success.",
                    show: true,
                    type: "success"
                })

                return saveAs(res.data.url, res.data.filename)
            }

            return setNotif({
                ...notif,
                title: "Error",
                message: "Failed to export data.",
                show: true,
                type: "error"
            })
        }).catch((err) => { return }) //do nothing, let it go back to login
    }

    return (
        <>
            <h1 className="mt-4">Report Performance Agent</h1>
            <ol className="breadcrumb mb-4">
                <li className="breadcrumb-item">Reports</li>
                <li className="breadcrumb-item active">Performance Agent</li>
            </ol>
            {notif.show && notif.type === 'error' && <Error
                title={notif.title}
                message={notif.message}
                show={notif.show}
                showChange={() => { setNotif(initialNotif) }}
            />}
            {notif.show && notif.type === 'success' && <Success
                title={notif.title}
                message={notif.message}
                show={notif.show}
                showChange={() => { setNotif(initialNotif) }}
            />}
            {notif.show && notif.type === 'warning' && <Warning
                title={notif.title}
                message={notif.message}
                show={notif.show}
                showChange={() => { setNotif(initialNotif) }}
            />}
            <Card className="mb-4">
                <Card.Header>
                    <i className="fas fa-table mr-1"></i> Filter Data
                </Card.Header>
                <Card.Body>
                    <Form onSubmit={handleSubmitFormFilter}>
                        <Form.Row>
                        <Form.Group className="col-md-2" controlId="DisableDate">
                                <Form.Label>Disable Date</Form.Label>
                                <br />
                                <Button variant="outline-info" onClick={() => handleChangeFormFilter("disable_date", !formFilter.disable_date)}>
                                    <i className={`far ${formFilter.disable_date ? "fa-check-square" : "fa-square"}`} />
                                </Button>
                            </Form.Group>
                            <Form.Group className="col-md-2" controlId="StartDate">
                                <Form.Label>From Date</Form.Label>
                                <DatePicker
                                    className="form-control"
                                    dateFormat="yyyy-MM-dd"
                                    selected={new Date(formFilter.start)}
                                    disabledKeyboardNavigation
                                    onChange={(date) => handleChangeFormFilter("start", formatDate(date) === false ? formatDate() : formatDate(date))}
                                    highlightDates={[new Date(), new Date()]}
                                    disabled={formFilter.disable_date}
                                />
                            </Form.Group>
                            <Form.Group className="col-md-2" controlId="EndDate">
                                <Form.Label>To Date</Form.Label>
                                <DatePicker
                                    className="form-control"
                                    dateFormat="yyyy-MM-dd"
                                    disabledKeyboardNavigation
                                    selected={new Date(formFilter.end)}
                                    onChange={(date) => handleChangeFormFilter("end", formatDate(date) === false ? formFilter.start : formatDate(date))}
                                    highlightDates={[new Date(), new Date()]}
                                    minDate={new Date(formFilter.start)}
                                    disabled={formFilter.disable_date}
                                />
                            </Form.Group>
                            <Form.Group className="col-md-2" controlId="Agent">
                                <Form.Label>Agent</Form.Label>
                                <CustomSelect.SelectBox
                                    optionSelect={optionAgent}
                                    onChangeValue={(value) => handleChangeFormFilter("user_id", value)}
                                    value={formFilter.user_id}
                                />
                            </Form.Group>
                            <Form.Group className="col-md-2" controlId="Campaign">
                                <Form.Label>Campaign</Form.Label>
                                <CustomSelect.SelectBox
                                    optionSelect={optionCampaign}
                                    onChangeValue={(value) => handleChangeFormFilter("campaign_id", value)}
                                    value={formFilter.campaign_id}
                                />
                            </Form.Group>
                        </Form.Row>
                        <Form.Row>
                            <Form.Group className="col-md-12 mb-0">
                                <Button type="submit" variant="primary">Search</Button>
                            </Form.Group>
                        </Form.Row>
                    </Form>
                </Card.Body>
            </Card>
            <Card className="mb-4">
                <Card.Body>
                    <Button variant="outline-secondary" onClick={() => {
                        setExporting(true)
                        handleExport(tableData.total_data)
                    }} disabled={!exporting && !loading && !isEmptyValue(tableData.data) ? false : true}>
                        {exporting && <Spinner animation="border" size="sm" className="mr-1" />} Export
                    </Button>
                    <hr />
                    <Table striped hover responsive width="100%">
                        <thead className="thead-dark">
                            <tr>
                                <th className="text-nowrap">No.</th>
                                <th className="text-nowrap">Agent</th>
                                <th className="text-nowrap">Campaign Name</th>
                                <th className="text-nowrap">Customer Name</th>
                                <th className="text-nowrap">Phone 1</th>
                                <th className="text-nowrap">Phone 2</th>
                                <th className="text-nowrap">Email</th>
                                <th className="text-nowrap">Card Number</th>
                                <th className="text-nowrap">Card Type</th>
                                <th className="text-nowrap">Limit</th>
                                <th className="text-nowrap">Tenor</th>
                                <th className="text-nowrap">Card Since Year</th>
                                <th className="text-nowrap">Card Since Month</th>
                                <th className="text-nowrap">Card Exp Date</th>
                                <th className="text-nowrap">CardEmbossName</th>
                                <th className="text-nowrap">Amount</th>
                                <th className="text-nowrap">Account No</th>
                                <th className="text-nowrap">Bank Interest</th>
                                <th className="text-nowrap">Mother Name</th>
                                <th className="text-nowrap">Age</th>
                                <th className="text-nowrap">Gender</th>
                                <th className="text-nowrap">Birth Place</th>
                                <th className="text-nowrap">Birth Date</th>
                                <th className="text-nowrap">Merchant</th>
                                <th className="text-nowrap">Transaction Date</th>
                                <th className="text-nowrap">Home Address</th>
                                <th className="text-nowrap">Home City</th>
                                <th className="text-nowrap">Home Zip</th>
                                <th className="text-nowrap">Office Address</th>
                                <th className="text-nowrap">Office City</th>
                                <th className="text-nowrap">Office Zip</th>
                                <th className="text-nowrap">Income</th>
                                <th className="text-nowrap">Business Type</th>
                                <th className="text-nowrap">Occupation</th>
                                <th className="text-nowrap">KTP</th>
                                <th className="text-nowrap">NPWP</th>
                                <th className="text-nowrap">Bank Name</th>
                            </tr>
                        </thead>
                        <tbody>
                            {loading &&
                                <tr>
                                    <td colSpan="37" className="text-center">
                                        <Spinner animation="border" size="sm" className="mr-1" />
                                        Loading data...
                                    </td>
                                </tr>
                            }
                            {!loading && isEmptyValue(tableData.data) &&
                                <tr>
                                    <td colSpan="37" className="text-center">
                                        <span className="text-danger">No data found</span>
                                    </td>
                                </tr>
                            }
                            {!loading && !isEmptyValue(tableData.data) &&
                                tableData.data.map((row, i) => (
                                    <tr key={ i }>
                                        <td>{ tableData.paging.index[i] }</td>
                                        <td>{ row.agent }</td>
                                        <td>{ row.campaign_name }</td>
                                        <td>{ row.customer_name }</td>
                                        <td>{ row.phone_1 }</td>
                                        <td>{ row.phone_2 }</td>
                                        <td>{ row.email }</td>
                                        <td>{ row.card_number }</td>
                                        <td>{ row.card_type }</td>
                                        <td>{ row.limits }</td>
                                        <td>{ row.tenor }</td>
                                        <td>{ row.card_since_year }</td>
                                        <td>{ row.card_since_month }</td>
                                        <td>{ row.card_exp_date }</td>
                                        <td>{ row.card_emboss_name }</td>
                                        <td>{ row.amount }</td>
                                        <td>{ row.account_no }</td>
                                        <td>{ row.bank_interest }</td>
                                        <td>{ row.mother_name }</td>
                                        <td>{ row.age }</td>
                                        <td>{ row.gender }</td>
                                        <td>{ row.birth_place }</td>
                                        <td>{ row.birth_date }</td>
                                        <td>{ row.merchant }</td>
                                        <td>{ row.transaction_date }</td>
                                        <td>{ row.home_address }</td>
                                        <td>{ row.home_city }</td>
                                        <td>{ row.home_zip }</td>
                                        <td>{ row.office_address }</td>
                                        <td>{ row.office_city }</td>
                                        <td>{ row.office_zip }</td>
                                        <td>{ row.income }</td>
                                        <td>{ row.business_type }</td>
                                        <td>{ row.occupation }</td>
                                        <td>{ row.ktp }</td>
                                        <td>{ row.npwp }</td>
                                        <td>{ row.bank_name }</td>
                                    </tr>
                                ))
                            }
                        </tbody>
                        <tfoot>
                            <tr>
                                <th>No.</th>
                                <th>Agent</th>
                                <th>Campaign Name</th>
                                <th>Customer Name</th>
                                <th>Phone 1</th>
                                <th>Phone 2</th>
                                <th>Email</th>
                                <th>Card Number</th>
                                <th>Card Type</th>
                                <th>Limit</th>
                                <th>Tenor</th>
                                <th>Card Since Year</th>
                                <th>Card Since Month</th>
                                <th>Card Exp Date</th>
                                <th>CardEmbossName</th>
                                <th>Amount</th>
                                <th>Account No</th>
                                <th>Bank Interest</th>
                                <th>Mother Name</th>
                                <th>Age</th>
                                <th>Gender</th>
                                <th>Birth Place</th>
                                <th>Birth Date</th>
                                <th>Merchant</th>
                                <th>Transaction Date</th>
                                <th>Home Address</th>
                                <th>Home City</th>
                                <th>Home Zip</th>
                                <th>Office Address</th>
                                <th>Office City</th>
                                <th>Office Zip</th>
                                <th>Income</th>
                                <th>Business Type</th>
                                <th>Occupation</th>
                                <th>KTP</th>
                                <th>NPWP</th>
                                <th>Bank Name</th>
                            </tr>
                        </tfoot>
                    </Table>

                    {!loading && !isEmptyValue(tableData.paging) &&
                         <Pagination
                            total={tableData.total_data}
                            limit={tableData.limit}
                            paging={tableData.paging}
                            pageChange={(page) => {
                                setCurrentFilter({
                                    ...currentFilter,
                                    page: page
                                })
                                setLoading(true)
                            }}
                        />
                    }
                </Card.Body>
            </Card>
        </>
    )
}

const initialNotif = {
    title: "",
    message: "",
    show: false,
    type: null
}