import { useState, useEffect, useCallback, useContext } from 'react'
import { Button, Form, Modal, Spinner } from 'react-bootstrap'
import { useForm, Controller } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import * as yup from 'yup'
import _ from 'lodash'
import { CustomSelect } from './../../components'
import { generalService } from './../../utils/services'
import { useOutboundCategories, useOutboundCategoryDetails } from './../../utils/functions'

const { isEmptyValue } = generalService

export const FormDetail = ({ modalChange, notifChange, dataChange, dataId }) => {
    const fnOutboundCategories = useOutboundCategories()
    const fnOutboundCategoryDetails = useOutboundCategoryDetails()
    const { handleSubmit, formState: { errors, isSubmitting }, control, register, reset, getValues } = useForm({
        defaultValues: {
            name: "",
            outbound_category_id: "",
            is_active: "1"
        },
        resolver: yupResolver(yup.object().shape({
            name: yup.string()
                .required("This field is required."),
            outbound_category_id: yup.string()
                .required("This field is required."),
        }))
    })
    const [optionOutboundCategory, setOptionOutboundCategory] = useState([{
        value: "",
        label: "Choose..."
    }])

    useEffect(() => {
        const fetchData = async () => {
            await fnOutboundCategoryDetails.Get({id: dataId}).then((res) => {
                let mapOutboundCategory = optionOutboundCategory.map((row) => {
                    return row.value
                })

                if (!isEmptyValue(res.data)) {
                    reset({
                        name: res.data.name || "",
                        outbound_category_id: (mapOutboundCategory.includes(res.data.outbound_category_id)) ? res.data.outbound_category_id : "",
                        is_active: res.data.is_active === 1 ? "1" : false
                    }, {keepErrors: false})
                }
            }).catch((err) => { return }) //do nothing, let it go back to login
        }

        if (!isEmptyValue(dataId)) return fetchData()
    }, [dataId, optionOutboundCategory])

    useEffect(() => {
        const fetchData = async () => {
            await fnOutboundCategories.Get({
                limit: 100,
                order: "name",
                is_active: 1
            }).then((res) => {
                if (!isEmptyValue(res.data)) {
                    let mapOption = res.data.map((row) => {
                        return { value: row.id, label: row.name }
                    })

                    setOptionOutboundCategory([
                        ...optionOutboundCategory,
                        ...mapOption
                    ])
                }
            }).catch((err) => { return }) //do nothing, let it go back to login
        }

        fetchData()
        return () => setOptionOutboundCategory([{
            value: "",
            label: "Choose..."
        }])
    }, [])

    const onSubmitForm = async (data, e) => {
        e.preventDefault()

        {Object.keys(data).forEach((key) => {
            if (['is_active'].includes(key) && data[key] === false) {
                data[key] = "0"
            }
        })}

        await fnOutboundCategoryDetails.Update(dataId, data).then((res) => {
            if (res.success) {
                notifChange({
                    title: "Success",
                    message: "Data has been updated.",
                    show: true,
                    type: "success"
                })
                dataChange()
            } else {
                notifChange({
                    title: "Error",
                    message: "Failed to update data.",
                    show: true,
                    type: "error"
                })
            }

            return modalChange()
        }).catch((err) => { return }) //do nothing, let it go back to login
    }

    return (
        <>
            <Modal.Header closeButton={isSubmitting ? false : true}>
                <Modal.Title>Detail of Outbound Category Detail</Modal.Title>
            </Modal.Header>
            <Form onSubmit={handleSubmit(onSubmitForm)}>
                <Modal.Body className="py-1">
                    <Form.Group controlId="Name">
                        <Form.Label>Name <span className="text-danger">*</span></Form.Label>
                        <Form.Control
                            type="text"
                            size="sm"
                            isInvalid={!!errors.name}
                            {...register("name")}
                        />
                        <Form.Control.Feedback type="invalid">{errors.name?.message}</Form.Control.Feedback>
                    </Form.Group>
                    <Form.Group controlId="OutboundCategory">
                        <Form.Label>Outboound Category <span className="text-danger">*</span></Form.Label>
                        <Controller
                            name={"outbound_category_id"}
                            control={control}
                            render={({ field: {value, onChange} }) => {
                                return (
                                    <CustomSelect.SelectBox
                                        optionSelect={optionOutboundCategory}
                                        onChangeValue={(value) => onChange(value)}
                                        value={getValues('outbound_category_id')}
                                        isError={!!errors.outbound_category_id ? true : false}
                                        isSmall={true}
                                    />
                                )
                            }}
                        />
                        <Form.Control.Feedback type="invalid">{errors.outbound_category_id?.message}</Form.Control.Feedback>
                    </Form.Group>
                    <Form.Group controlId="IsActive">
                        <Form.Check
                            type="checkbox"
                            label="Active"
                            custom
                            value="1"
                            {...register("is_active")}
                        />
                    </Form.Group>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="primary" type="submit" disabled={isSubmitting}>
                        {isSubmitting && <Spinner animation="border" size="sm" className="mr-1" />} Save
                    </Button>
                    <Button variant="light" disabled={isSubmitting} onClick={modalChange}>Close</Button>
                </Modal.Footer>
            </Form>
        </>
    )
}