import React, { useContext, useEffect, useState } from 'react'
import { Row, Button, Form, Modal, Spinner, Table } from 'react-bootstrap'
import DatePicker from 'react-datepicker'
import _ from 'lodash'
import { FormAdd } from './FormAdd';
import { useCallbacks } from '../../utils/functions';
import { Pagination, Notification } from '../../components';
import { generalService } from '../../utils/services';
import { AuthContext } from '../../utils/context';


const { isEmptyValue, formatDate } = generalService

export const Callback = (props) => {
    const { userId, userLevelId } = useContext(AuthContext)
    const { Get } = useCallbacks()
    const { Error, Success, Warning } = Notification
    const [isLoading, setIsLoading] = useState(true)
    const [notif, setNotif] = useState(initialNotif)
    const [modalAdd, setModalAdd] = useState(initialModal)
    const [modalDetail, setModalDetail] = useState(initialModal)
    const [tableData, setTableData] = useState({})
    const [disableDate, setDisableDate] = useState(false)

    const user_id = userLevelId == 1 ? userId : ''

    const [formFilter, setFormFilter] = useState({
        customer_name: "",
        campaign_name: "",
        start: formatDate(),
        end: formatDate(),
        user_id: user_id
    })
    const [currentFilter, setCurrentFilter] = useState({
        page: 1,
        ...formFilter
    })

    const handleModalAdd = (customer_id, campaign_id, phone_no) => {
        setModalAdd({
            ...modalAdd,
            show: !modalAdd.show,
            customerId: customer_id,
            campaignId: campaign_id,
            phoneNo: phone_no,
        })
    }

    const handleModalDetail = (id) => {
        setModalDetail({
            ...modalDetail,
            show: !modalDetail.show,
            dataId: id
        })
    }

    const handleChangeFormFilter = (key, val) => {
        if (['start', 'end'].includes(key)) {
            val = formatDate(val)
        }
        if (key === 'disable_date') {
            val ? setDisableDate(true) : setDisableDate(false)
        }
        setFormFilter({ ...formFilter, [key]: val })
    }

    const handleSubmitFormFilter = async (e) => {
        e.preventDefault()
        await setIsLoading(true)
        setCurrentFilter({
            page: 1,
            ...formFilter
        })
    }

    const handleModalClose = async () => {
        setModalAdd(initialModal)
        setModalDetail(initialModal)
    }

    useEffect(() => {
        const fetchData = async () => {
            await Get(currentFilter).then(res => {
                setTableData(res)
                setIsLoading(false)
            }).catch((err) => { return })
        }
        fetchData()
    }, [isLoading, currentFilter])

    return (
        <>
            <h1 className="mt-4">Scheduled Calls</h1>
            <ol className="breadcrumb mb-4">
                <li className="breadcrumb-item active">Scheduled Calls</li>
            </ol>
            <div className="card mb-4">
                <div className="card-header">
                    <i className="fas fa-table mr-1"></i> Filter Data
                </div>
                <div className="card-body">
                    <form onSubmit={handleSubmitFormFilter}>
                        <Row>
                            <Form.Group className="col-md-2" controlId="DisableDate">
                                <Form.Label>Disable Date</Form.Label>
                                <br />
                                <span className="btn btn-sm btn-outline-dark">
                                    <Form.Check type="checkbox" name="disable_date" className="text-center" onClick={e => handleChangeFormFilter('disable_date', e.target.checked)} />
                                </span>
                            </Form.Group>
                            <Form.Group className="col-md-2" controlId="StartDate">
                                <Form.Label>Start Date</Form.Label>
                                <DatePicker
                                    selected={new Date(formFilter.start)}
                                    dateFormat="yyyy-MM-dd"
                                    className="form-control"
                                    placeholderText="YYYY-MM-DD"
                                    name="start"
                                    showMonthDropdown
                                    showYearDropdown
                                    onChange={date => handleChangeFormFilter("start", date)}
                                    disabled={disableDate}
                                />
                            </Form.Group>

                            <Form.Group className="col-md-2" controlId="EndDate">
                                <Form.Label>End Date</Form.Label>
                                <DatePicker
                                    selected={new Date(formFilter.end) >= new Date(formFilter.start) ? new Date(formFilter.end) : new Date(formFilter.start)}
                                    dateFormat="yyyy-MM-dd"
                                    className="form-control"
                                    placeholderText="YYYY-MM-DD"
                                    name="end"
                                    minDate={new Date(formFilter.start)}
                                    // minDate={new Date()}
                                    showMonthDropdown
                                    showYearDropdown
                                    onChange={date => handleChangeFormFilter("end", date)}
                                    disabled={disableDate}
                                />
                            </Form.Group>

                            <Form.Group className="col-md-2" controlId="EndDate">
                                <Form.Label>Campaign Name</Form.Label>
                                <input type="text" id="inputState" className="form-control" onChange={e => handleChangeFormFilter("campaign_name", e.target.value)} />
                            </Form.Group>

                            <Form.Group className="col-md-2" controlId="EndDate">
                                <Form.Label>Customer Name</Form.Label>
                                <input type="text" id="inputState" className="form-control" onChange={e => handleChangeFormFilter("customer_name", e.target.value)} />
                            </Form.Group>

                        </Row>
                        <button type="submit" className="btn btn-primary">Search</button>
                    </form>
                </div>
            </div>
            <div className="card mb-4">
                <div className="card-body">
                    {/* <ExportToExcel rawData={exportData} fileName="user-data" />&nbsp; */}
                    {/* <Button variant="outline-primary" onClick={handleShow}>Add Data</Button> */}
                    <hr />
                    <Table striped hover responsive width="100%" cellSpacing="0" cellPadding="0">
                        <thead className="thead-dark">
                            <tr>
                                <th width="">Scheduled At</th>
                                <th width="">Campaign</th>
                                <th width="">Customer Name</th>
                                <th width="">Agent</th>
                                <th width="">Phone Number</th>
                                <th width="">Reason</th>
                                <th width="">Called</th>
                                <th width="">Outbound Status</th>
                                <th width="" className="text-center">Action</th>
                            </tr>
                        </thead>
                        <tfoot>
                            <tr>
                                <th width="">Scheduled At</th>
                                <th width="">Campaign</th>
                                <th width="">Customer Name</th>
                                <th width="">Agent</th>
                                <th width="">Phone Number</th>
                                <th width="">Reason</th>
                                <th width="">Called</th>
                                <th width="">Outbound Status</th>
                                <th width="" className="text-center">Action</th>

                            </tr>
                        </tfoot>
                        <tbody>
                            {isLoading ? (
                                <tr>
                                    <td colSpan="9" className="text-center">
                                        <Spinner animation="border" size="sm" className="mr-1" />
                                        Loading data...
                                        </td>
                                </tr>
                            ) : (
                                    tableData.total_data > 0 ? (
                                        tableData.data.map((item, i) => (
                                            <tr key={item.id} className="text-center">
                                                <td>{item.scheduled_at}</td>
                                                <td>{item.campaign_name}</td>
                                                <td>{item.customer_name}</td>
                                                <td>{item.agent}</td>
                                                <td>{item.phone_number}</td>
                                                <td>{item.reason}</td>
                                                <td>{item.called}</td>
                                                <td>{item.outbound_status}</td>
                                                <td>
                                                    {(userLevelId == 1) ? (
                                                        <Button  variant="primary" size="sm" className="m-1" data-toggle="modal" onClick={(e) => { handleModalAdd(item.customer_id, item.campaign_id, item.phone_number) }}>
                                                            <i className="fas fa-plus fa-fw"></i>
                                                        </Button>
                                                    ) : (
                                                            <Button disabled variant="primary" size="sm" className="m-1" data-toggle="modal" onClick={(e) => { handleModalAdd(item.customer_id, item.campaign_id, item.phone_number) }}>
                                                                <i className="fas fa-plus fa-fw"></i>
                                                            </Button>
                                                        )}
                                                </td>
                                            </tr>
                                        ))
                                    ) : (
                                            <>
                                                <tr>
                                                    <td colSpan="8" className="text-center"><span className="text-danger">No data found</span></td>
                                                </tr>
                                            </>
                                        )
                                )}
                        </tbody>
                    </Table>
                    {isLoading === false && !_.isEmpty(tableData) &&
                        <Pagination
                            total={tableData.total_data}
                            limit={tableData.limit}
                            paging={tableData.paging}
                            pageChange={async (pageNumber) => {
                                await setIsLoading(true)
                                setCurrentFilter({
                                    ...currentFilter,
                                    page: pageNumber
                                })
                            }}
                        />
                    }
                </div>
            </div>
            <Modal show={modalAdd.show} onHide={handleModalClose} backdrop="static" keyboard={false} >
                <Modal.Header closeButton>
                    <Modal.Title>Schedule Call</Modal.Title>
                </Modal.Header>
                <FormAdd
                    customerId={modalAdd.customerId}
                    campaignId={modalAdd.campaignId}
                    phoneNo={modalAdd.phoneNo}
                    handleModalClose={handleModalClose}
                    modalChange={(params) => {
                        setModalAdd({
                            ...modalAdd,
                            ...params
                        })
                    }}
                    notifChange={(value) => {
                        setNotif(value)
                    }}
                    dataChange={async () => {
                        await setIsLoading(true)
                    }}
                    isLoading={isLoading}
                    setIsLoading={setIsLoading}
                />
            </Modal>
        </>
    )
}

const initialNotif = {
    title: "",
    message: "",
    show: false,
    type: null
}

const initialModal = {
    show: false,
    dataId: null
}

const initialOption = [{
    value: "",
    label: "Choose..."
}]
